<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col>
          <el-form ref="formRef" :model="queryVo" label-width="80px" inline>
            <el-form-item label="" prop="teaName">
              <el-input
                v-model="queryVo.teaName"
                @keyup.enter.native="queryPage"
                placeholder=""
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="deptName">
              <el-input
                v-model="queryVo.deptName"
                @keyup.enter.native="queryPage"
                placeholder=""
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="teaDuties">
              <el-input
                v-model="queryVo.teaDuties"
                @keyup.enter.native="queryPage"
                placeholder=""
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" icon="el-icon-search" @click="queryPage"
                ></el-button
              >
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>

      <el-row :gutter="10" class="el-row">
        <el-col :span="1.5">
          <el-button
            type="primary"
            icon="el-icon-plus"
              v-if="is_authority.is_user_add"
            size="mini"
            @click="gotoAdd"
            ></el-button
          >
          
        </el-col>
        
        <el-col :span="1.5">
          <el-button
            type="success"
            icon="el-icon-edit"
            size="mini"
            :disabled="single"
            @click="editUser"
            v-if="is_authority.is_user_upt"
            ></el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            :disabled="multiple"
            v-if="is_authority.is_user_del"
            @click="del"
            ></el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button type="info" icon="el-icon-upload2" size="mini"
            ></el-button
          >
          <!-- @click="handleImport" -->
          <!-- v-hasPermi="['system:user:import']" -->
        </el-col>
        <el-col :span="1.5">
          <el-button type="warning" icon="el-icon-download" size="mini"
          @click="handleExport"
            ></el-button
          >
                      <!-- v-hasPermi="['system:user:export']" -->
        </el-col>
        <!-- <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar> -->
      </el-row>

      <!--  -->
      <el-table
        :data="List"
        border
        stripe
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="50" align="center" />

        <el-table-column
          type="index"
          width="80"
          align="center"
          label=""
        ></el-table-column>

        <el-table-column
          prop="teaId"
          label=""
          align="center"
        ></el-table-column>

        <el-table-column
          label=""
          align="center"
        >
        <template slot-scope="scope">
          <img
                @click="photo(scope.row)"
                :src="scope.row.teaPhotoUrl"
                style="width: 50px;height: 50px"
              >
        </template>
        </el-table-column>

        <el-table-column
          prop="teaName"
          label=""
          align="center"
        ></el-table-column>

        <el-table-column
          prop="teaSex"
          label=""
          align="center"
        ></el-table-column>

        <el-table-column
          prop="teaDuties"
          label=""
          align="center"
        ></el-table-column>

        <el-table-column
          prop="deptName"
          label=""
          align="center"
        ></el-table-column>

        <el-table-column
          prop="teaStaffroomId"
          label="id"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="majorName"
          label=""
          align="center"
        ></el-table-column>

        <el-table-column label="" width="180px" align="center">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              v-if="is_authority.is_user_upt"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="gotoUpt(scope.row)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""  v-if="is_authority.is_user_del"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope.row)"
              ></el-button>
            </el-tooltip>

            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="warning"
                icon="el-icon-document"
                size="mini"
                @click="gotoDetail(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page.current"
        :page-sizes="[5, 10, 20, 30, 40]"
        :page-size="page.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
      ></el-pagination>
      <!--  :before-close="handleClose" -->
      <!--  -->
      <!-- <el-dialog
        title=""
        :visible.sync="open"
        width="50%"
        @close="closeInit"
      >
        

        <el-form
          :model="Form"
          :rules="rules"
          ref="formRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="" prop="teaName">
                <el-input
                  v-model="Form.teaName"
                  placeholder=""
                ></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="" prop="teaId">
                <el-input
                  v-model="Form.teaId"
                  placeholder=""
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="" prop="teaSex">
                <el-input
                  v-model="Form.teaSex"
                  placeholder=""
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="teaDuties">
                <el-input
                  v-model="Form.teaDuties"
                  placeholder=""
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-form-item label="id" prop="teaStaffroomId">
            <el-input
              v-model="Form.teaStaffroomId"
              placeholder="id"
            ></el-input>
          </el-form-item>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="" prop="teaDepartId">
                <el-select
                  v-model="Form.teaDepartId"
                  placeholder=""
                  filterable=""
                >
                  <el-option
                    v-for="item in deptOptions"
                    :key="item.id"
                    :label="item.deptName"
                    :value="item.deptId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="" prop="teaMjaorId">
                <el-select
                  v-model="Form.teaMjaorId"
                  placeholder=""
                  filterable=""
                >
                  <el-option
                    v-for="item in majorOptions"
                    :key="item.id"
                    :label="item.majorName"
                    :value="item.majorId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="" prop="teaPhoto">
                <el-upload
                  :file-list="fileList"
                  class="upload-demo"
                  action="http://www.dayanhulian.com:8001/matter/fileoss/uploadOssFile"
                  :headers="config"
                  :on-success="saveUrl"
                  :before-upload="beforeUpload"
                  accept=".jsp,.png"
                  >
                  <el-button size="small" type="primary"></el-button>
                  <div slot="tip" class="el-upload__tip">jpg/png</div>
                </el-upload>
              </el-form-item>
            </el-col>
          </el-row>


        </el-form>
        
        <span slot="footer" class="dialog-footer">
          <el-button @click="open = false"> </el-button>
          <el-button type="primary" @click="submitForm"> </el-button>
        </span>
      </el-dialog> -->
    </el-card>

    <el-dialog
          :visible.sync="dialogVisible">
          
          <img width="100%" :src="photoView" >

        </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      fileList:[],
      token: window.sessionStorage.getItem('token'),
      photoView:'',
      dialogVisible: false,
      deptOptions: [],
      majorOptions: [],
      Form: {},
      ids: [],
      open: false,
      // 
      single: true,
      // 
      multiple: true,
      List: [],
      queryVo: {
        teaName: undefined,
        page: {
          current: 1,
          size: 5
        }
      },
      page: {
        current: 1,
        size: 5,
        total: 0
      },
      rules: {
        teaDepartId: [
          { required: true, message: '', trigger: 'blur' }
        ],
        teaMjaorId: [
          { required: true, message: '', trigger: 'blur' }
        ],
        teaId: [
          { required: true, message: '', trigger: 'blur' }
        ],
        teaName: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapState(['btus']),
  },
  created() {
    this.isAuthority()
    this.queryPage()
    this.getTreeselect()
  },
  methods: {
    photo(row){
      this.dialogVisible = true
      this.photoView=row.teaPhotoUrl
    },
    gotoAdd(){
      this.$router.push({path: '/teaUpt'})
    },
    gotoDetail(row){
      // const id = row.id
      this.$router.push({ path: '/teaDtl', query: { data: row} })

    },
    gotoUpt(row){
      this.$router.push({path: '/teaUpt',query:{ data : row}})
    },
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    async handleExport(){
      console.log(111);
      this.$http.get('/teaBaseInfo/exportTeaBaseInfos')
    },
    
    
    async getTreeselect() {
      const { data: res } = await this.$http.post(
        '/majorInfo/queryMajorInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      const { data: res1 } = await this.$http.post(
        '/deptInfo/queryDeptInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      const arr = res.data.dataList.records
      this.majorOptions = this.unique(arr)
      this.deptOptions = res1.data.dataList.records
      if (res.code != 20000) return this.$message.error(res.message)
      // console.log(this.deptOptions)
      // console.log(this.majorOptions)
    },

    unique(arr) {
      const res = new Map()
      return arr.filter(arr => !res.has(arr.id) && res.set(arr.id, 1))
    },
    submitForm() {
      this.$refs.formRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post(
            '/teaBaseInfo/saveOrUpdateTeaBaseInfo',
            this.Form
          )
          if (res.code != 20000) return this.$message.error(res.message)
          this.open = false
          this.queryPage()
          return this.$message.success(res.message)
        }
      })
    },

    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    },

    async editUser(row) {
      this.open = true
      const id = row.id || this.ids
      console.log(id)
      const { data: res } = await this.$http.get(
        '/teaBaseInfo/getByIdTeaBaseInfo/' + id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      console.log(res)
      this.Form = res.data.entity
    },

    del(row) {
      if (row.teaId) {
        var a = row.teaId.split(',')
      }
      const userid = a || this.teaId
      this.$confirm(
        '   ' + userid + ' ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      )
        .then(async () => {
          const { data: res } = await this.$http.delete(
            '/teaBaseInfo/removeByIdTeaBaseInfos',
            {
              data: userid
            }
          )
          // console.log(res);
          this.queryPage()
          this.List = []
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '',
            type: 'success'
          })
        })
    },
    async queryPage() {
      const { data: res } = await this.$http.post(
        '/teaBaseInfo/queryTeaBaseInfoList',
        this.queryVo
      )
      console.log(res.data.dataList.records)
      if (res.code != 20000) return this.$message.error(res.message)
      this.List = res.data.dataList.records
      this.page.total = res.data.dataList.total
      this.page.current = res.data.dataList.current
      this.page.size = res.data.dataList.size
      // console.log(this.List)
    },
    //
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length != 1
      this.multiple = !selection.length
    },
    //
    async uptStatus(status) {
      const { data: res } = await this.$http.put(
        `/jxyxUser/users/${status.id}/state/${status.mgState}`
      )
      if (res.code != 20000) {
        status.mgState = !status.mgState
        return this.$message.error(res.message)
      }
      return this.$message.success(res.message)
    },
    // 
    handleSizeChange(newSize) {
      this.queryVo.page.size = newSize
      this.queryPage()
    },
    //
    handleCurrentChange(newCurrent) {
      this.queryVo.page.current = newCurrent
      this.queryPage()
    },
    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    }
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
.el-row {
  display: flex;
  align-items: center;
}
.el-pagination {
  margin-top: 15px;
}
</style>
